"use client";
import React, { useState } from 'react';
import Link from 'next/link';
import { GraduationCap, Menu, X } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import styles from './Navbar.module.css';

const Navbar = () => {
    const [isExpanded, setIsExpanded] = useState(false);

    return (
        <div className={styles.navbarWrapper}>
            <motion.nav
                className={styles.dynamicIsland}
                layout
                onHoverStart={() => setIsExpanded(true)}
                onHoverEnd={() => setIsExpanded(false)}
                transition={{ type: "spring", stiffness: 300, damping: 25, mass: 0.8 }}
            >
                <motion.div layout className={styles.logoIcon}>
                    <GraduationCap size={18} />
                </motion.div>

                <AnimatePresence mode="popLayout">
                    {isExpanded && (
                        <motion.div
                            className={styles.expandedContent}
                            initial={{ opacity: 0, width: 0 }}
                            animate={{ opacity: 1, width: 'auto' }}
                            exit={{ opacity: 0, width: 0 }}
                            transition={{ duration: 0.3, ease: [0.32, 0.72, 0, 1] }}
                        >
                            <motion.div className={styles.navLinks} layout>
                                <Link href="/" className={styles.link}>Platform</Link>
                                <Link href="/" className={styles.link}>Solutions</Link>
                                <Link href="/" className={styles.link}>Resources</Link>
                            </motion.div>
                            <Link href="/login" className={styles.ctaBtn}>
                                Sign in
                            </Link>
                        </motion.div>
                    )}
                </AnimatePresence>

                {!isExpanded && (
                    <motion.div
                        layout
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        className="px-2 font-medium text-sm"
                        transition={{ duration: 0.2 }}
                    >
                        UniERP
                    </motion.div>
                )}
            </motion.nav>
        </div>
    );
};

export default Navbar;
